<template>
  <div id = "container">
    <div class="createRoom" v-if="creating">
      <div class="close" @click="this.creating=false;">
        <el-button size="mini" type="danger" icon="el-icon-close"></el-button>
      </div>
      <div class="createRoomInput">
        <el-input v-model="roomName" placeholder="请输入房间名"></el-input>
      </div>
      <div class="createRoomButton" @click="createRoom">
        <el-button>创建房间</el-button>
      </div>
    </div>
    <div class="addProblem" v-if="adding">
      <div class="close" @click="this.adding = false;">
        <el-button size="mini" type="danger" icon="el-icon-close"></el-button>
      </div>
      <div class="msg">
        {{this.msg}}
      </div>
      <div class="input">
        <el-input v-model="key" placeholder="请输入密钥"></el-input>
      </div>
      <div class="input">
        <el-input v-model="problem" placeholder="请输入问题"></el-input>
      </div>
      <div class="input">
        <el-input v-model="hint1" placeholder="请输入提示1(可为空)"></el-input>
      </div>
      <div class="input">
        <el-input v-model="hint2" placeholder="请输入提示2(可为空)"></el-input>
      </div>
      <div class="input">
        <el-input v-model="hint3" placeholder="请输入提示3(可为空)"></el-input>
      </div>
      <div class="addProblemButton" @click="addProblem">
        <el-button>添加问题</el-button>
      </div>
    </div>
    <div id = "rooms">
    <Room v-for="room in rooms" :key="room.id" :id="room.id" :name="room.name" :players="room.playerNum" @click="enterRoom(room.id)"></Room>
    </div>
    <el-button id="displayCreateRoom" type="primary" @click="this.creating = true;this.adding=false;">
      创建房间
    </el-button>
    <el-button id="displayAddProblem" type="primary" @click="this.adding = true;this.creating=false;">添加问题</el-button>
    <el-button id="refresh" type="primary" @click="this.$axios.get('/rooms').then((response)=>{
        this.rooms = response.data;
      });">刷新</el-button>
  </div>
</template>

<script>
import Room from './components/Room.vue'
import { ref } from 'vue'

export default {
  name: 'App',
  components: {
    Room
  },
  setup(){
    return{
      roomName:ref('')
    }
  },
  data(){
    return {
      rooms:[],
      creating:false,
      maxplayer:8,
      adding:false,
      key:"",
      problem:"",
      hint1:"",
      hint2:"",
      hint3:"",
      msg:""
    }
  },
  methods:{
    createRoom:function(){
      this.$axios.get("/createRoom",{
        params: {
          roomName:this.roomName
        }
      })
      .then((response)=>{
        if(response.status == 200 && typeof response.data === "number")
        {
          sessionStorage.setItem("roomId",response.data);
          window.location.href="/room/"+response.data;
        }
      });
    },
    addProblem:function(){
      this.$axios.get("/addProblem",{
        params: {
          key:this.key,
          problem:this.problem,
          hint1:this.hint1,
          hint2:this.hint2,
          hint3:this.hint3
        }
      }).then((response)=>{
        if(response.status == 200 && response.data != "")
        {
          this.msg = response.data;
        }
      });
    },
    enterRoom:function(roomId){
      sessionStorage.setItem("roomId",roomId);
      window.location.href="/room/"+roomId;
    }
  },
  mounted:function(){
    this.$axios.get("/rooms").then((response)=>{
        this.rooms = response.data;
      });
  },
}

</script>

